.flag-icon
{
    line-height: 1em;

    position: relative;

    display: inline-block;

    width: 1.333333em;

    background-repeat: no-repeat;
    background-position: 50%;
    -webkit-background-size: contain;
    background-size: contain;
}
.flag-icon:before
{
    content: '\00a0';
}
.dropdown-menu .flag-icon
{
    margin-right: 5px;
}

